@import url(https://fonts.googleapis.com/css2?family=Kameron&display=swap);
* {
  margin: 0 auto;
}
header{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.navPanel {
  background-color: #404040;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 0 auto;
  align-items: center;
  justify-content: flex-end;
  vertical-align: middle;

  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  height: 65px;
  color: white;
}
.links {
  width: 70%;
  height: 70%;
  justify-content: flex-end;
  margin: 0;
  display: flex;
  align-items: center;
  vertical-align: middle;

}
form {
  width: 40%;
  position: relative;
}
.text-input {
  height: 35px;
  width: 100%;
  border-style: none;
  border-radius: 5px;
}
.submit-button {
  font-size: 0;
  position: absolute;
  background-color: white;
  z-index: 2;
  top: 2px;
  right: 0;
  height: 90%;
  width: 5%;
  border-style: none;
  background-size: 90%;
  background-repeat: no-repeat;
}
.links > a {
  font-size: 24px;
  text-decoration: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.links > a:visited {
  color: white;
  text-decoration: none;
}

.searchbar {
  height: 100%;
  width: 300px;
  border-radius: 5px;
  border-style: none;
}
textarea:focus,
input:focus {
  outline: none;
}

.shop-link,
.cart-link,
.sign-up-link,
.log-in-link,
.wishlist-link {
  width: 200px;
  height: 100%;
  margin: 0;

  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}
.shop-link{
  border-left: 1px solid white;
  

}
.cart-link{
  border-right: 1px solid white;
  border-left: 1px solid white;

}
.cart-link,
.wishlist-link,
.shop-link {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  height: 100%;
}

#wishlist-count, #cart-count {
  margin: 0;
  float: left;
  font-size: 12px;
  font: Arial, Helvetica;
  width: 10px;
  height: 10px;
  line-height: 10px;
  color: white;
  padding: 5px;
  background-color: #E31837;
  border-radius: 20px;
  text-align: center;
  font-weight: 600;
  position: relative;
  left: 15px;
  top: -5px;
}
.custom-search-botton {
  height: 35px;
  position: absolute;
  right: -4px;
  top: 0px;
  bottom: 3px;
  border: 0;
  color: black;
  outline: none;
  margin: 0;
  padding: 0 10px;
  z-index: 2;
}
.search-block {
  position: relative;
  height: 60%;
  width: 300px;
  border-radius: 5px;
  border-style: none;
}

.nav-icon{
  margin: 0 ;
  padding: 7px;
  border-radius: 3px;  
  display: inline;

}
.nav-icon:hover{
  background-color: #565656;
}
.hidden{
  display: none;
}
#shop-icon{
  margin: auto
}

.temp-cart {
  display: flex;
  width: 50%;
}
form {
  width: 50%;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
}
.submit-form-button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  background-color: #265a88;
  width: 200px;
  height: 50px;
  border-style: none;
  border-radius: 5px;
  color: white;
  font-size: 26px;
}
.cart-container {
  box-shadow: 0 2px 5px rgb(48 48 48 / 5%);
  padding: 20px;
}
.cart-products-container{ 
  box-shadow: 0px 0px 7px -4px rgba(0,0,0,0.68);
  width: 50%;
  margin-right: 0;
}
.cart-item-row {
  width: 100%;
  height: 100px;
  display: flex;
  padding-bottom: 5px;
  padding-top: 10px;

  border-bottom: 1px solid rgb(219, 216, 216);
}
.item-cart-price {
  font-size: 22px;
  font-family: Arial, "Segoe UI", Helvetica, sans-serif;
}
.image-block {
  width: 15%;
  height: 100%;
}
.mini-image {
  height: 100%;
  width: 100%;
}
.information-block {
  width: 75%;
}
.description-block {
  width: 100%;
  display: flex;
  font: 18px Roboto, sans-serif;
  margin-bottom: 10px;
}
.price-block {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.item-price,
.item-total-price {
  margin: 0;
  font-size: 20px;
  font-family: Kameron;
  width: 100px;
}
.quantity-block {
  display: flex;
  margin-right: 40px;
  border: 1px solid rgb(238, 234, 234);
  border-radius: 3px;
  height: 30px;
  width: 100px;
}

.plus-button {
  margin: 0;

  border: none;
  background-color: white;
  width: 25%;
  height: 100%;
  border-right: 1px solid rgb(238, 234, 234);
}
.minus-button {
  margin: 0;
  border: none;
  height: 100%;
  width: 25%;

  background-color: white;

  border-left: 1px solid rgb(238, 234, 234);
}
.item-quantity {
  margin-top: 7px;
}
.main-cart {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.total-sum-block {
  width: 100%;
  margin-top: 30px;
  font: 22px Kameron;
  display: flex;
  justify-content: space-between;
}
.cart-sum-text{
  width: auto;
  margin-left: 10px;
}
.cart-sum-number{
  width: auto;
  margin-right: 60px;

}
#cart-form{
  width: 500px;
  display: block;
 padding: 20px;
 border: 1px solid grey;

}
#cart-form>input{
  border: none;
  border-bottom: 1px solid grey;
  width: 50%;
}
#cart-form>label{
  font-size: 20px;
  width: 150px;
  display:inline-block


}

.remove-item{
  width: 20px;
  height: 20px;
  background-image: url(/static/media/remove1.42cf9f6f.svg);
  border-style: none;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 5px;
  background-color: white;

}
main {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  margin-left: 10%;
  margin-right: 10%;
  background-color: #fcfeff;
}
.product-card {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 200px;
  height: 300px;
  margin: 2px;
  border-radius: 5px;
  padding: 5px;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgb(0 0 0 / 7%);
  transition: all 0.3s ease;
}
.product-card:hover {
  box-shadow: 0 0 10px rgb(0 0 0 / 40%);
}

.product-card > img {
  width: 100%;
  height: 50%;
}
.description {
  width: 100%;
  font: 16px Roboto, sans-serif;
}
.price {
  width: 35%;
  color: #444;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 18px;
  font-weight: 600;
}

.cart-button {
  color: white;
  border-style: none;
  border-radius: 5px;
  padding: 0;
  width: 42px;
  height: 42px;
  background-image: url(/static/media/shoppingCart.5d5d94c1.svg);
  background-size: 85%;
  background-repeat: no-repeat;
  background-color: white;
}

button:hover {
  cursor: pointer;
}
.cart-button:hover {
  background-color: rgb(233, 231, 231);
}

.temp-cart {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.title {
  height: 60px;
  border-bottom: 1px solid #e1e8ee;
  padding: 20px 30px;
  color: #5e6977;
  font-size: 18px;
  font-weight: 400;
}


.cart-description {
  flex-grow: 1;
}
.wishlist-button{
  height: 35px;
  border: none;
  border-radius: 5px;
}


  .wishlist-product-card{
    display:flex;
  width: 50%;
  border-bottom: 1px solid rgb(197, 189, 189);
  margin-bottom: 10px;
  }
  .wishlist-product-card>.cart-button{
    margin-right: 10px
  }
  .wishlist-product-card>img{
    width: 60px;
  }
  .wishlist-container{
    margin-top: 50px;
  }
  
.categories-block{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: start;
    align-items: center;
}
.category{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 0 5px;
    padding: 5px;
    font-family: Arial, Helvetica, sans-serif;
    
    border: 1px solid #E0E0E0;
    border-radius: 3px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.category:hover{
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    color: red;
    cursor: pointer;
}
